import React, { Component } from "react";

class One extends Component {
  constructor() {
    super();
    this.state = {
      name: "state props"
    };
  }
  myclick() {
    alert("alert okay");
  }

  render() {
    return (
      <div>
        <p>js app page {this.state.name}</p>
        <button onClick={() => this.myclick()}>click me</button>
      </div>
    );
  }
}

export default One;
